import { useState, useEffect, } from 'react';
import { Container, Menu, } from 'semantic-ui-react';
import { useMediaPredicate } from 'react-media-hook';

import { Web3Button, Web3NetworkSwitch } from '@web3modal/react';
import { useAccount } from 'wagmi';
import { getNetwork } from '@wagmi/core';
import { defaultChainId } from '../../configs';


const mobileBreakpoint = '(max-width: 500px)';



function AppHeader() {
  const isMobile = useMediaPredicate(mobileBreakpoint);
  const { isConnected } = useAccount();
  const { chain } = getNetwork();

  const [isWrongNetwork, setWrongNetwork] = useState(false);
  useEffect(() => {
    if (isConnected && chain && chain.id === defaultChainId) {
      setWrongNetwork(false);
    } else if (isConnected && chain && chain.id !== defaultChainId) {
      setWrongNetwork(true);
    } else {
      setWrongNetwork(false);
    }
  }, [isConnected, chain]);

  return (
    <header>
      <Menu borderless fixed='top'>
        <Container>
          <Menu.Item header as={'a'} href={''}>
            {isMobile
              ? 'Web3Mason.io'
              : 'Web3Mason.io'
            }
          </Menu.Item>
          {isWrongNetwork
            ? <Menu.Item position='right'><Web3NetworkSwitch /></Menu.Item>
            : <Menu.Item position='right'>
              <Web3Button
                icon='show'
                label='Connect Wallet'
                balance={isMobile ? 'hide' : 'show'}
              />
            </Menu.Item>
          }
        </Container>
      </Menu>
    </header>
  );
}

export default AppHeader;
