import { useState, useEffect, } from 'react';
import { Container, Grid, Header, Form, Input, Button, Label, } from 'semantic-ui-react';
import BigNumber from 'bignumber.js';
import { parseEther, formatEther, } from 'ethers';
import { toast } from 'react-toastify';

import { Web3Button, Web3NetworkSwitch, } from '@web3modal/react';
import { useAccount, } from 'wagmi';
import {
  getNetwork, watchBlockNumber, fetchBalance, fetchFeeData,
  //fetchToken, prepareSendTransaction,
  readContract, writeContract, //prepareWriteContract, 
  waitForTransaction,
} from '@wagmi/core';
import {
  defaultChainId,
  addressW3mToken, addressW3mTokenPresale,
} from './configs';
import ConnectWalletButton from './components/ConnectWalletButton';
import PresaleAbi from './abi/PresaleAbi.json';
import getExternalLinkProps from './utils/getExternalLinkProps';
import { DefaultLayout } from './layouts';
import './App.scoped.scss';



BigNumber.set({ ROUNDING_MODE: BigNumber.ROUND_DOWN, DECIMAL_PLACES: 18, });

const w3mTokenData = {
  address: '0xcB289B4200BA390D3e13f49dC78A076539F3A044',
  name: 'Web3Mason', symbol: 'W3M', decimals: 18,
  totalSupply: {
    formatted: '100000000000', value: 100000000000000000000000000000n,
  },
};
const priceStep = (0.0000001).toFixed(7);


function App() {
  const { address, isConnected } = useAccount();
  const { chain } = getNetwork();

  const [blockNumber, setBlockNumber] = useState(0);
  const [feeData, setFeeData] = useState({
    gasPrice: 0n, maxFeePerGas: 0n, maxPriorityFeePerGas: 0n,
    formatted: { gasPrice: '0', maxFeePerGas: '0', maxPriorityFeePerGas: '0', },
  });
  const txGas = 100000n;
  const [txCostEth, setTxCostEth] = useState(txGas * 25000000000n);
  const [balanceEth, setBalanceEth] = useState({
    decimals: 18, symbol: 'ETH',
    formatted: '0.0', value: 0n,
  });
  const [reserveW3m, setReserveW3m] = useState({
    formatted: '5000000000.0', value: 5000000000000000000000000000n,
  });


  const [priceEth, setPriceEth] = useState(priceStep);
  const [amountEth, setAmountEth] = useState('1');
  const [amountW3m, setAmountW3m] = useState('10000000');


  const [isWrongNetwork, setWrongNetwork] = useState(false);
  useEffect(() => {
    if (isConnected && chain && chain.id === defaultChainId) {
      setWrongNetwork(false);
    } else if (isConnected && chain && chain.id !== defaultChainId) {
      setWrongNetwork(true);
    } else {
      setWrongNetwork(false);
    }
  }, [isConnected, chain]);


  const handleChangeAmountEth = (e, { name, value }) => {
    if (value === '') {
      return setAmountEth('0');
    }
    if (parseFloat(value) === NaN || parseFloat(value) === 0) {
      return setAmountEth(value);
    }
    if (parseFloat(value) < 0.0000001) {
      return setAmountEth(priceStep);
    }
    if (balanceEth.value === 0n) {
      return setAmountEth(parseFloat(value).toFixed(7));
    }

    value = parseFloat(parseFloat(value).toFixed(7)).toString();
    if (parseEther(value) > (balanceEth.value - txCostEth)) {

      return setAmountEth(
        parseFloat(formatEther(balanceEth.value - txCostEth)).toFixed(7)
      );
    }
    return setAmountEth(value);
  }
  const setAmountEthMax = (e) => {
    e.preventDefault();
    if (balanceEth.value < txCostEth) {
      return setAmountEth('0');
    }
    setAmountEth(
      parseFloat(formatEther(balanceEth.value - txCostEth)).toFixed(7)
    );
  }


  useEffect(() => {
    let amountW3m = BigNumber(amountEth).dividedBy(priceEth).toString();
    if (parseFloat(amountW3m) > parseFloat(reserveW3m.formatted)) {
      amountW3m = reserveW3m.formatted;
      const amountEth = BigNumber(reserveW3m.formatted).multipliedBy(priceEth).toString();
      setAmountW3m(amountW3m);
      setAmountEth(amountEth);
      return;
    }
    setAmountW3m(amountW3m);
  }, [amountEth, priceEth, reserveW3m]);


  const onSubmitBuy = async () => {
    if (chain.id !== defaultChainId) {
      return;
    }
    if (parseFloat(amountEth) < 0.0000001) {
      return;
    }

    try {
      const { hash } = await writeContract({
        address: addressW3mTokenPresale,
        abi: PresaleAbi,
        chainId: defaultChainId,

        account: address,
        functionName: 'buy',
        args: [],
        value: parseEther(amountEth),
      });
      const toastId = toast.loading(
        <div>🚀 Pending Tx: <a
          href={`${etherscan(defaultChainId)}/tx/${hash}`}
          {...getExternalLinkProps()}
        >{hash}</a></div>
      );
      const transactionReceipt = await waitForTransaction({ chainId: defaultChainId, hash, });
      toast.done(toastId);

    } catch (error) {
      toast.error('🤔 Something wrong...');
    }
  }


  useEffect(() => {
    if (!isConnected) return;

    readContract({
      address: addressW3mTokenPresale,
      abi: PresaleAbi,
      chainId: defaultChainId,
      functionName: 'reserveToken',
      //args: [],
    })
      .then((reserveW3m) => {
        setReserveW3m({
          formatted: formatEther(reserveW3m), value: reserveW3m,
        });
      })
      .catch((error) => { console.error(error); });

    fetchBalance({ address, chainId: defaultChainId, })
      .then((balanceEth) => {
        setBalanceEth(balanceEth);
      })
      .catch(null);

    fetchFeeData({ chainId: defaultChainId, })
      .then((feeData) => {
        setFeeData(feeData);
        setTxCostEth(txGas * feeData.maxFeePerGas * 110n / 100n);
      })
      .catch(null);
  }, [blockNumber]);


  useEffect(() => {
    const amountW3m = BigNumber(amountEth).dividedBy(priceEth).toString();
    setAmountW3m(amountW3m);

    const unwatchBlockNumber = watchBlockNumber(
      { chainId: defaultChainId, listen: true, },
      (blockNumber) => {
        setBlockNumber(blockNumber);
      }
    );
    return () => {
      unwatchBlockNumber();
    };
  }, []);

  return (
    <DefaultLayout
      title={null}
      description={null}
      meta={[]}
    >
      <Container>
        <Grid textAlign='center' style={{ height: '90vh' }} verticalAlign='middle'>
          <Grid.Column style={{ maxWidth: 450 }}>
            <Header textAlign='center'>
              <Header.Content>
                Web3Mason Token (W3M) Presale
                <Header.Subheader>
                  <div style={{ color: '#00b5ad', }}>
                    <br />
                    <p><b>🔥 Early access discount 90%!</b></p>
                    <p><b>⚡️ Only the first 5% of the total supply!</b></p>
                    <br />
                  </div>
                </Header.Subheader>
              </Header.Content>
            </Header>

            <Form onSubmit={onSubmitBuy} widths='equal'>
              <Form.Field
                control={Input}
                type='number' name='priceEth' label='Price for 1 W3M, ETH' readOnly
                value={priceEth}
              />
              <Form.Field
                control={Input}
                type='number' name='amountW3m' label={`You will receive, W3M`} readOnly
                value={amountW3m}
              />
              <Label style={{ marginTop: '-10px', marginBottom: '10px', fontSize: '8px', }}>
                Reserve: {parseFloat(reserveW3m.formatted).toLocaleString('en')}
              </Label>

              <Form.Field
                control={Input}
                type='number' step={priceStep}
                name='amountEth' label='Purchase amount, ETH' placeholder='1.1' required
                value={amountEth}
                onChange={handleChangeAmountEth}
                action={{
                  color: 'teal',
                  content: 'MAX',
                  onClick: setAmountEthMax,
                }}
              />
              <Label style={{ marginTop: '-10px', marginBottom: '10px', fontSize: '8px', }}>
                Gas Fees: {parseFloat(formatEther(txCostEth)).toFixed(7)} ETH
              </Label>
              <br />

              {isWrongNetwork
                ? <Web3NetworkSwitch />
                : isConnected
                  ? <Button type='submit' color='teal'>Buy W3M</Button>
                  : <ConnectWalletButton label={'Buy W3M'} color='teal' />
              }
            </Form>

            <br /><br /><hr />
            <p>Token Smart Contract: <a
              href={`${etherscan(defaultChainId)}/token/${addressW3mToken}`}
              {...getExternalLinkProps()}
            >{addressW3mToken}</a></p>
            <p>Presale Smart Contract: <a
              href={`${etherscan(defaultChainId)}/address/${addressW3mTokenPresale}`}
              {...getExternalLinkProps()}
            >{addressW3mTokenPresale}</a></p>
          </Grid.Column>
        </Grid>
      </Container>
    </DefaultLayout >
  );
}

export default App;

function etherscan(chainId) {
  if (chainId === 5) {
    return 'https://goerli.etherscan.io';
  }
  return 'https://etherscan.io';
}
